.join-waitlist {
  max-width: 382px;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.20);
  backdrop-filter: blur(6.650000095367432px);
  border-radius: 28px;
  padding: 20px 8px 8px;
  margin: 0 auto;
  color: #fff;
  font-family: GothamPro, sans-serif;
  pointer-events: auto;
  letter-spacing: 0;
  @include sm {
    padding: 16px 4px 4px;
    max-width: 340px; }
  &__title {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.2;
    margin: 0 0 12px;
    text-align: center;
    text-transform: uppercase;
    @include sm {
      font-size: 14px; }
    @include xs {
      font-size: 12px; } }
  &__form {
    background: #fff;
    border-radius: 100px;
    display: flex;
    padding: 4px;
    justify-content: space-between;
    gap: 10px;
    @include sm {
      gap: 4px; } }
  &__input {
    background: transparent;
    border: none;
    display: block;
    color: #000;
    width: 100%;
    min-width: 1px;
    font-size: 16px;
    height: 42px;
    font-family: GothamPro, sans-serif;
    font-weight: 400;
    line-height: 1;
    flex: 1 1 auto;
    padding: 0 10px 0 16px;
    border-radius: 100px;
    @include sm {
      font-size: 14px;
      height: 38px; }
    &::placeholder {
      color: #919191; } }
  &__btn {
    border-radius: 100px;
    min-width: 136px;
    background: var(--button-background);
    color: var(--button-color);
    font-size: 14px;
    font-weight: 700;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 42px;
    font-family: GothamPro, sans-serif;
    text-transform: uppercase;
    line-height: 1;
    flex: none;
    transition: background 0.2s ease, color 0.2s ease;
    @include sm {
      min-width: 110px;
      height: 38px; }
    .icon {
      display: none; }
    &:hover {
      background: var(--button-background-hover);
      color: var(--button-color-hover); } } }
