.s-discount-form {
  padding: clamp(50px, 5vw, 100px) 0;
  background: $black;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: 50%;
    top: -45px;
    transform: translateX(-50%);
    margin-left: -648px;
    width: 378px;
    height: 467px;
    background: url(../img/dollar-sign.png) center center no-repeat;
    background-size: cover;

    @include tablet {
      margin-left: -498px; } }


  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -90px;
    transform: translateX(-50%);
    margin-left: 675px;
    width: 450px;
    height: 457px;
    background: url(../img/at-sign.png) center center no-repeat;
    background-size: cover;

    @include tablet {
      margin-left: 525px; } } }

.discount-form {
  position: relative;
  z-index: 1;

  &__title {
    text-align: center;
    font-size: clamp(26px, 3vw, 32px);
    margin: 0 auto clamp(32px, 5vw, 65px);
    max-width: 500px;
    font-weight: 600;

    em {
      font-style: normal;
      background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font-weight: 700; } }


  &__form {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin: 0 auto; }

  &__field {

    &:not(:first-child) {
      margin: 24px 0 0;

      @include md {
        margin: 14px 0 0; }

      &.textarea {
        margin: 24px 0 0;

        @include md {
          margin: 14px 0 0; } } } }

  &__input,
  &__textarea {
    @include regular;
    width: 100%;
    min-height: 68px;
    line-height: 1.24;
    background: $gray;
    border-radius: 15px;
    padding: 22px 32px;
    font-size: 18px;
    min-width: 100%;
    max-width: 100%;
    display: block;
    border: none;
    color: $white;

    @include sm {
      min-height: 52px;
      padding: 16px 22px;
      font-size: 16px; } }


  &__textarea {
    min-height: 200px; }

  &__submit {
    margin: clamp(32px, 4vw, 64px) auto 0;
    display: inline-flex;
    @extend .btn--border;
    @include regular; } }


