.techs {
	display: flex;
	gap: 10px;
	margin: 32px 0;
	position: relative;
	justify-content: center;

	@include sm {
		margin-left: -20px;
		margin-right: -20px;
		padding: 0 20px 10px;
		justify-content: flex-start;
		overflow: hidden;
		overflow-x: auto; }

	&__item {
		display: flex;
		margin: 0;
		gap: 15px 20px;
		flex: 1 1 25%;
		max-width: 25%;
		min-width: 260px;
		flex-direction: column;
		padding: 0;
		border: 1px solid #1d1d1d;
		border-radius: 10px;
		background: #1d1d1d;
		padding: 25px 20px;
		@include sm {
			width: 240px;
			flex: none; }
		@include xs {
			padding: 20px 15px; } }

	&__link {
		text-decoration: none;
		color: #fff;
		transition: color .1s;

		&:hover {
			text-decoration: underline;
			color: $themeColor; }
		a {
			color: #F60;
			display: inline-flex;
			align-items: flex-start;
			gap: 12px;
			font-size: 14px;
			line-height: 1.5;
			text-decoration: none;
			@include md {
				font-size: 12px; }

			&:hover {
				text-decoration: none;
				span {
					border-bottom-color: transparent; } }

			span {
				border-bottom: 1px solid; }

			.icon {
				flex: none;
				fill: none;
				font-size: 18px; } } }

	&__img-box {
		height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0;
		flex: 0 0 auto;
		display: none;

		@include xs {
			margin: 0 0 0; }
		picture {
			display: block;
			height: 100%; }
		img {

			display: block;
			height: 100%;
			width: 100%;
			object-fit: contain; } }

	&__title {
		font-size: 18px;
		font-weight: 700;
		@include sm {
			font-size: 16px; } }

	&__text {
		color: rgba(255,255,255,0.7);
		font-size: 12px;
		ul {
			list-style-type: disc;
			line-height: 1.4;
			margin-left: 20px;
			li {
				margin-bottom: 5px;
				&:last-child {
					margin-bottom: 0; } } } } }
