.hero {
	position: relative;
	min-height: 100vh {
 }  //min-height: calc(var(--vh, 1vh) * 100)
	padding: 30px 0 80px;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover {
 }  //background-image: url(../img/hero-overlay.png)
	background-color: #1e2220;
	z-index: 1;
	pointer-events: none;

	&.hero_teams {
		position: relative;
		padding-top: 33%;
		min-height: 0;
		background: url(../img/hero.png) center top no-repeat;
		background-size: cover;
		margin-bottom: -20%;
		z-index: -1;

		@include md {
			margin-top: 80px; }

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(180deg, rgba(18, 18, 18, 0) 0%, $mainBg 90%); } }

	&.hero_inner {
		min-height: 0;
		background: url(/img/h-bg.svg) right bottom no-repeat;
		background-color: #181818; }

	@include tablet {
		padding: 45px 0 45px; }
	@include md {
		padding: 60px 0 60px; }
	@include sm {
		padding: 20px 0 20px; }

	&__video {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;

		z-index: -1;
		filter: brightness(0.7);
		object-fit: cover;
		display: none;

		@include sm {
			display: none; } }

	&__bg {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -1;
		background: url(../img/hero.jpg) center center no-repeat;
		background-size: cover;
		filter: brightness(0.7);
		display: none; }

	&__inner {
		z-index: 2;
		pointer-events: none;
		@extend .container; }

	&__logo {
		width: 80px;
		margin: 0 auto clamp(40px, 5vw, 58px); }

	&__suptitle {
		text-transform: uppercase;
		font-size: clamp(14px, 3vw, 20px);
		font-weight: 600;
		margin-bottom: clamp(32px, 4vw, 40px);
		text-align: center;

		p {
			&:not(:first-child) {
				margin-top: clamp(10px, 1.2vw, 18px); } } }

	&__switch {
		margin-bottom: clamp(20px, 4vw, 32px);
		pointer-events: auto; }
	&__title {
		font-size: clamp(36px, 6.6vw, 100px);
		line-height: 1.06;
		text-align: center;
		max-width: 1400px;
		margin: 0 auto;
		font-weight: 800;
		@media (min-width: 1025px) and (max-height: 860px) {
			font-size: 80px;
			max-width: 1100px; }

		strong {
			font-weight: 800;
			background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent; }
		span {
			display: block;
			font-size: 60%;
			line-height: 1.2;
			padding-top: 12px; } }

	&__btn {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 10px 20px;
		margin-top: clamp(32px, 4vw, 40px);
		@include sm {
			gap: 10px 15px; }
		.btn {
			pointer-events: auto;
			min-width: 240px;

			@include xs {
				flex: 1;
				padding-left: 10px;
				padding-right: 10px;
				font-size: 12px;
				min-width: 130px; } } }

	&__partner {
		position: absolute;
		right: 50%;
		transform: translateX(660px);
		top: 10px;
		font-size: 12px;
		line-height: 1.4;
		pointer-events: auto;
		z-index: 2;

		@include laptop {
			transform: translateX(0);
			right: 10px;
			top: 10px; }

		@include tablet {
			right: 20px; }

		@include md {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			right: 0;
			margin: 32px auto; } }

	&__partners {
		display: flex;
		margin-top: 4px; }

	&__partner-img {
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			height: 50px;
			width: auto;
			@include sm {
				height: 40px; } } }

	&__timer {
		display: none;
		position: absolute;
		padding: 0 4vw;
		right: 0;
		left: 0;
		bottom: 1vw;
		font-size: 15vw;
		font-weight: 600;
		color: $themeColor;
		opacity: .2;
		line-height: 1;
		white-space: nowrap;

		.live {
			text-align: center; } } }

.hero-inner {
	padding: 60px 0 60px;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	text-align: center;
	min-height: 400px;
	display: flex;
	align-items: center;
	position: relative;
	z-index: 1;
	@include md {
		padding: 80px 0 40px;
		min-height: 200px; }

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		background-color: #000;
		opacity: .35; }

	.container {
		display: grid;
		width: 100%;
		gap: 32px; }
	&__title {
		font-size: 64px;
		line-height: 1.2;
		@include black;
		@include md {
			font-size: 52px; }
		@include sm {
			font-size: 32px; } }
	&__logo {
		width: 112px;
		display: block;
		margin-left: auto;
		margin-right: auto;
		@include md {
			display: block; }
		@include sm {
			width: 80px; }
		img {
			width: 100%;
			display: block; } }
	&__date {
		font-size: 20px;
		@include md {
			font-size: 16px; } }

	&--schedule {
		padding: 40px 0 !important;
		min-height: 180px !important;
		text-align: left;
		background-image: url(../img/screens-done-op.svg) !important;
		background-position: center top !important;
		@include sm {
			text-align: center; }

		.hero-inner__logo {
			display: block;
			margin-left: auto;
			margin-right: auto;
			grid-column: 2/3;
			@include sm {
				grid-column: auto; } }
		.hero-inner__title {
			display: none; }

		.hero-inner__date {
			@include sm {
				order: 1; } }
		.container {
			grid-template-columns: 1fr auto 1fr;
			align-items: center;
			gap: 24px;
			@include sm {
				grid-template-columns: 1fr; } } } }

.h-list {
	display: flex;
	justify-content: space-between;
	max-width: 720px;
	margin: 0 auto clamp(26px, 4vw, 54px);
	padding: 0;

	@media screen and (max-height: 768px) {
		margin: 0 auto 26px; }

	&_centered {
		justify-content: center; }

	&_min {
		max-width: 411px; }

	@include md {
		max-width: 600px;
		padding: 0; }
	@include xs {
		flex-wrap: wrap;
		justify-content: center;
		padding: 0; }

	&__item {
		display: flex;
		flex-direction: column;

		@include md {
			flex-direction: column;
			text-align: center; }

		@include sm {
			width: 50%;
			align-items: center;
			text-align: center; }
		&:not(:last-child) {
			margin-right: 40px;
			@include sm {
				margin-right: 0; } }
		@include xs {
			margin-bottom: 9px; }
		&:nth-child(2n) {
			@include xs {
				margin-right: 0; } }

		@include xs {
			max-width: 90px;

			width: auto;
			max-width: unset; } }

	&__value {
		font-size: clamp(32px, 4.6vw, 48px);
		margin: 0 20px 0 0;
		@include bold;
		display: flex;
		align-items: center;
		@include md {
			margin: 0;
			justify-content: center; }
		@include sm {
			justify-content: flex-start; }
		@include xs {
			margin-bottom: 0;
			line-height: 1.1; }

		.icon-infinity {
			width: 71px;
			height: 60px;
			fill: #FFF; } }

	&__text {
		font-size: 14px;
		line-height: 1.4;
		padding: 10px 0;
		opacity: 0.4;
		&-top {
			font-size: 20px;
			@include medium;
			text-transform: uppercase;
			@include tablet {
				font-size: 16px; }
			@include xs {
				font-size: 14px; } }
		@include xs {
			font-size: 12px; }
		@include tablet {
			padding: 9px 0; }
		@include md {
			padding: 0; } }
	&__icon {
		margin-top: 10px;
		margin-left: 8px;
		font-size: 30px;
		&_anim {
			animation: heart 2s infinite; }
		.icon {
			fill: $white; }
		@include sm {
			margin-top: 6px;
			.icon {
				width: 0.9em; } } } }

@keyframes heart {
	0% {
		transform: scale(1); }
	50% {
		transform: scale(1.2); }
	100% {
		transform: scale(1); } }

.logo {
	display: block;
	width: 100%;
	pointer-events: auto;
	img {
		//height: 100%
		width: 100%;
		display: block;
		border-radius: 10px;
		box-shadow: 0 3px 8px rgba(0, 0, 0, 0.5); } }
