.schedule-btn-wrap {
  padding: clamp(50px, 5vw, 100px) 0;
  border-bottom: 1px solid $gray3;

  @include xs {
    padding: 30px 0; }

  .container {
    width: 100%; } }

.shedule-v2 {
  --accentColor: #fff !important; }

.talks-list {
  padding: 30px 0 0;
  .s-item {
    background: #131513;
    border-left: 1px solid #202421;
    border-right: 1px solid #202421;
    &__row {
      display: flex;
      border-bottom: 1px solid #202421;
      @include sm {
        flex-wrap: wrap; }
      & + & {
        margin-top: 20px; } }
    &__col {
      padding: 16px 28px;
      border-right: 1px solid #202421;
      @include last {
        border: none; }
      &_d-sm-none {
        @include sm {
          display: none; } }
      @include md {
        padding: 16px 18px; }
      &:nth-child(1) {
        text-align: center;
        width: 30%;
        @include sm {
          text-align: left;
          width: 100%;
          border: none;
          padding: 16px; } }
      &:nth-child(2) {
        width: 70%;
        @include sm {
          width: 100%;
          margin-top: 10px;
          margin: 0;
          padding: 16px;
          padding-top: 0; } } } } }

.talks {
  padding: 149px 0 97px;
  border-bottom: 1px solid #2c302d;
  @include laptop {
    padding: 55px 0 60px; }

  a {
    color: #fff; }
  @include sm {
    border-bottom: none;
    padding-bottom: 20px; }
  .container {
    @include sm {
      padding: 0 10px; } }
  .s-item {
    background: transparent;
    border: none;
    max-width: 1200px;
    margin: 0 auto;
    @include first {
      margin: 0; }

    &__col {
      border: none;
      @include sm {
        padding: 0 20px;
        margin: 16px 0 0;
        @include first {
          margin: 0; } } }

    &__text {
      line-height: 1.6;
      margin-top: 10px;

      a {
        color: #fff; } }
    &__title {
      @include regular; } }

  .talk__item {
    margin-top: 6px;
    @include first {
      margin-top: 0; }
    @include sm {
      margin-top: 16px;
      @include first {
        margin-top: 0; } }
    @include xs {
      margin: 10px -10px;
      @include first {
        margin-top: 0; } } } }
