.s-virtual-activities {
  position: relative;
  padding: clamp(50px, 5vw, 100px) 0;
  background: $black;
  background-image: url(data:image/png;base64,);
  background-size: 12px 12px;
  border-bottom: 1px solid $gray;

  .section__title {
    font-size: clamp(32px, 5vw, 64px);
    font-weight: 800;
    margin-bottom: clamp(24px, 4vw, 38px); }

  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: url(../img/virtual-activities.png) center center no-repeat;
    background-size: 1250px 265px; } }


.virtual-activities {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;

  &__desc {
    max-width: 800px;
    margin: 0 auto;
    opacity: 0.4;
    font-size: clamp(16px, 2vw, 20px);
    line-height: 1.33;
    text-align: center;

    p {
      &:not(:first-child) {
        margin: 26px 0 0; } } }

  &__btn {
    @extend .btn--border;
    display: inline-flex;
    margin: clamp(24px, 5vw, 64px) auto 0; } }

